w .info{max-width:501px}.news .new .info .blockTitle{display:flex;justify-content:space-between;align-items:center;margin-bottom:30px}.news .new .info .blockTitle h3{font-size:22px;font-weight:400}.news .new .info .blockTitle p{font-size:16px;font-weight:300}.news .new .info .paragraph{font-weight:300;font-size:18px;line-height:31px;margin-bottom:30px}.news .new .info .readMore{display:inline-block}.news .new .info .readMore a{display:block;font-size:17px;font-weight:300;color:#02e502;text-decoration:none;margin-bottom:6px}.news .new .info .readMore .line{display:block;width:100%;height:1px;background-color:#02e502}.paging{margin-bottom:110px}.paging ul{width:100%;display:flex;justify-content:center}.paging ul li{font-size:40px;font-weight:400;margin-right:30px;color:rgba(255,255,255,.2);cursor:pointer}.paging ul li:last-child{margin-right:0}.paging ul .active{color:#02e502}