.phtotography__wrapper{margin-bottom:110px}.headline{position:relative;font-size:105px;color:rgba(255,255,255,.2);margin-bottom:60px;margin-bottom:30px}.headline::before{content:"Процесс аэрофотосъемки";position:absolute;top:6px;left:5px;height:100%;display:flex;align-items:center;font-size:43px;font-weight:300;color:#02e502}@media(max-width: 768px){.headline{font-size:64px}.headline::before{font-size:30px}}.describe{font-size:18px;font-weight:300;line-height:31px;margin-bottom:60px;max-width:750px}.tablet{display:flex;align-items:center;flex-wrap:wrap}.tablet .tabletImg{margin-right:94px}.tablet .works{width:388px}.tablet .works ul li{display:flex;align-items:center;font-size:21px;font-weight:300;margin-bottom:30px}.tablet .works ul li:last-child{margin-bottom:50px}.tablet .works ul li img{width:24px;height:24px;margin-right:12px}.tablet .works button{width:287px;height:68px;font-family:"RF Rufo";background-color:rgba(0,0,0,0);font-size:28px;font-weight:300;color:#02e502;border:1px #02e502 solid;position:relative;overflow:hidden;z-index:2;cursor:pointer;transition:.3s ease-out}.tablet .works button::after{content:"";display:block;width:100px;height:100px;position:absolute;top:50%;left:50%;transform:translate(-50%, -50%) scale(0);transform-origin:center;background-color:#02e502;border-radius:50%;z-index:-1;transition:.4s}.tablet .works button:hover{color:#000}.tablet .works button:hover::after{transform:translate(-50%, -50%) scale(3)}.tablet .worksType{margin-bottom:50px}.tablet .worksType .worksType_wrapper{display:flex;justify-content:space-between;font-size:24px;font-weight:400;margin-bottom:12px}.tablet .worksType .worksType_wrapper p{cursor:pointer}.tablet .worksType .line{position:relative;width:100%;height:4px;background-color:#464646}.tablet .worksType .line::before{content:"";position:absolute;display:block;height:100%;width:50%;top:0;left:0;background-color:#02e502;transition:left .5s}.tablet .worksType .activeLine::before{left:50%}