.team__wrapper{margin-bottom:110px}.headline{position:relative;font-size:105px;color:rgba(255,255,255,.2);margin-bottom:60px}.headline::before{content:"Наша команда";position:absolute;top:6px;left:5px;height:100%;display:flex;align-items:center;font-size:43px;font-weight:300;color:#02e502}@media(max-width: 768px){.headline{font-size:64px}.headline::before{font-size:30px}}.team{display:flex;flex-wrap:wrap}.team .person{width:372px;margin-right:17px}.team .person:last-child{margin-right:0}.team .person span{display:block;position:relative;width:330px;margin:0 auto;margin-top:20px;margin-bottom:47px}.team .person span::before,.team .person span::after{content:"";position:absolute;display:block;width:116px;height:116px;background-color:rgba(0,0,0,0);border-top:1px #02e502 solid;border-right:1px #02e502 solid}.team .person span::after{top:-17px;right:-20px}.team .person span::before{bottom:-17px;left:-20px;transform:rotate(180deg)}.team .person span img{width:100%;height:100%}.team .person h3{font-size:34px;font-weight:400;margin-bottom:26px}.team .person .position{font-size:24px;font-weight:300;margin-bottom:30px}.team .person .about{font-size:24px;line-height:31px;font-weight:300}