.regions__wrapper{position:relative;margin-bottom:20px}.regions__wrapper img{position:absolute;display:block;width:850px;top:-35%;right:-16%}.headline{position:relative;font-size:105px;color:rgba(255,255,255,.2);margin-bottom:60px}.headline::before{content:"Регионы присутствия";position:absolute;top:6px;left:5px;height:100%;display:flex;align-items:center;font-size:43px;font-weight:300;color:#02e502}@media(max-width: 768px){.headline{font-size:64px}.headline::before{font-size:30px}}.ourPlaces{display:flex;position:relative}.ourPlaces .leftText{position:absolute;top:50%;left:-148px;display:inline-block;writing-mode:vertical-rl;white-space:nowrap;font-size:100px;font-weight:700;text-transform:uppercase;color:rgba(70,70,70,.15);text-shadow:-3px 4px rgba(0,0,0,.12);transform-origin:top;transform:rotate(180deg) translateY(-50%)}.ourPlaces .firstUl{margin-right:75px}.ourPlaces .region{font-weight:300;font-size:24px;line-height:39px}