.news__wrapper{margin-bottom:110px}.headline{position:relative;font-size:105px;color:rgba(255,255,255,.2);margin-bottom:60px}.headline::before{content:"Новости";position:absolute;top:6px;left:5px;height:100%;display:flex;align-items:center;font-size:43px;font-weight:300;color:#02e502}@media(max-width: 768px){.headline{font-size:64px}.headline::before{font-size:30px}}.news__container{display:flex;flex-wrap:wrap}.news__container .news{position:relative;width:363px;height:607px;margin-right:20px}.news__container .news .img__wrapper{position:relative;width:316px;margin:0 auto;margin-bottom:50px;margin-top:20px}.news__container .news .img__wrapper img{display:block;width:100%;height:100%}.news__container .news .img__wrapper::before,.news__container .news .img__wrapper::after{content:"";position:absolute;display:block;width:116px;height:116px;background-color:rgba(0,0,0,0);border-top:1px #02e502 solid;border-right:1px #02e502 solid}.news__container .news .img__wrapper::after{top:-17px;right:-20px}.news__container .news .img__wrapper::before{bottom:-17px;left:-20px;transform:rotate(180deg)}.news__container .news .da{display:inline-flex;flex-direction:column}.news__container .news h3{font-size:21px;margin-bottom:30px}.news__container .news p{font-size:18px;font-weight:300;line-height:31px;margin-bottom:30px}.news__container .news span{position:absolute;top:calc(100% - 46px);left:0}.news__container .news .readMore{position:relative;font-size:18px;font-weight:300;color:#02e502;text-decoration:none}.news__container .news .readMore::before{content:"";position:absolute;display:block;width:100%;height:1px;background-color:#02e502;top:calc(100% + 4px);left:0}