.wrapper{margin:0 auto;max-width:1150px}.spraying__wrapper{margin-bottom:110px}.headline{position:relative;font-size:105px;color:rgba(255,255,255,.2);margin-bottom:60px}.headline::before{content:"Преимущества опрыскивания с помощью БПЛА";position:absolute;top:6px;left:5px;height:100%;display:flex;align-items:center;font-size:43px;font-weight:300;color:#02e502}@media(max-width: 768px){.headline{font-size:64px}.headline::before{font-size:30px}}.benefits{display:flex;flex-wrap:wrap;align-items:baseline;position:relative}.benefit{min-width:370px;background-color:#1e1d1d;margin-right:20px;position:relative}.benefit .subtitle{position:absolute;top:50%;left:-148px;display:inline-block;writing-mode:vertical-rl;white-space:nowrap;font-size:100px;font-weight:700;text-transform:uppercase;color:rgba(70,70,70,.15);text-shadow:-3px 4px rgba(0,0,0,.12);transform-origin:top;transform:rotate(180deg) translateY(-50%)}.benefit:last-child{margin-right:0}.benefit .benefit__wrapper{width:310px;margin:0 auto}.benefit .benefit__wrapper .title{display:flex;align-items:center;margin:50px 0 16px 0}.benefit .benefit__wrapper .title img{display:block;width:36px;height:36px;margin-right:15px}.benefit .benefit__wrapper .title h3{font-size:22px;font-weight:300}.benefit .benefit__wrapper .content{margin-top:40px;margin-bottom:66px}.benefit .benefit__wrapper .content ul li{display:flex;align-items:baseline;margin-bottom:20px}.benefit .benefit__wrapper .content ul li img{width:12px;height:12px;margin-right:14px}.benefit .benefit__wrapper .content ul li p{font-size:18px;font-weight:300}.benefit .benefit__wrapper .content ul li:last-child{margin-bottom:0}.benefit hr{width:100%;height:1px;background-color:#02e502}