.products__wrapper{margin-bottom:110px}.headline{position:relative;font-size:105px;color:rgba(255,255,255,.2);margin-bottom:60px}.headline::before{content:"Наша продукция";position:absolute;top:6px;left:5px;height:100%;display:flex;align-items:center;font-size:43px;font-weight:300;color:#02e502}@media(max-width: 768px){.headline{font-size:64px}.headline::before{font-size:30px}}.products{display:flex;flex-wrap:wrap;position:relative}.products .leftText{position:absolute;top:50%;left:-148px;display:inline-block;writing-mode:vertical-rl;white-space:nowrap;font-size:100px;font-weight:700;text-transform:uppercase;color:rgba(70,70,70,.15);text-shadow:-3px 4px rgba(0,0,0,.12);transform-origin:top;transform:rotate(180deg) translateY(-50%)}.products .product__wrapper{width:364px;height:599px;background-color:#1e1d1d;margin-right:19px}.products .product img{display:block;max-width:300px;height:229px;margin:0 auto 27px auto}.products .product h3{font-size:33px;font-weight:300;margin-bottom:30px}.products .product .main__info{max-width:288px;margin:0 auto}.products .product .description{font-size:22px;font-weight:300;margin-bottom:20px}.products .product .description p:first-child{margin-bottom:13px}.products .product .price{font-size:30px;font-weight:300;margin-bottom:50px}.products .product .add{width:288px;height:62px;font-family:"RF Rufo";background-color:rgba(0,0,0,0);font-size:28px;font-weight:300;color:#02e502;border:1px #02e502 solid;position:relative;overflow:hidden;z-index:2;cursor:pointer;transition:.3s ease-out}.products .product .add::after{content:"";display:block;width:100px;height:100px;position:absolute;top:50%;left:50%;transform:translate(-50%, -50%) scale(0);transform-origin:center;background-color:#02e502;border-radius:50%;z-index:-1;transition:.4s}.products .product .add:hover{color:#000}.products .product .add:hover::after{transform:translate(-50%, -50%) scale(3)}