.help__wrapper{width:100%;background-color:#1e1d1d;padding-bottom:116px;margin-bottom:110px}.help__container{display:flex;justify-content:center;position:relative}.help__container .help__content{position:relative;z-index:1}.help__container .droneBg{content:"";position:absolute;top:-117px;right:0;display:block;width:46%;height:96%;z-index:2}.help__container .linesBg{content:"";position:absolute;top:-24%;right:-3%;display:block;width:67%;height:203%;z-index:1}.help__container .headline{position:relative;font-size:105px;color:rgba(255,255,255,.2);margin-bottom:60px;margin-bottom:29px;margin-top:30px}.help__container .headline::before{content:"Нужна помошь?";position:absolute;top:6px;left:5px;height:100%;display:flex;align-items:center;font-size:43px;font-weight:300;color:#02e502}@media(max-width: 768px){.help__container .headline{font-size:64px}.help__container .headline::before{font-size:30px}}.help__container .formInfo{font-size:24px;font-weight:300;line-height:40px;max-width:494px;margin-bottom:39px}.help__container .form{display:flex;align-items:center;margin-bottom:20px}.help__container .form input{color:#fff;font-size:18px;background-color:rgba(0,0,0,0);border:none;outline:none;border-bottom:1px #4b4a4a solid;padding-bottom:11px;padding-left:11px;margin-right:42px}.help__container .form input::placeholder{color:#fff;font-size:18px;margin-left:10px}.help__container .form button{width:197px;height:58px;font-family:"RF Rufo";background-color:rgba(0,0,0,0);font-size:28px;font-weight:300;color:#02e502;border:1px #02e502 solid;position:relative;overflow:hidden;z-index:2;cursor:pointer;transition:.3s ease-out}.help__container .form button::after{content:"";display:block;width:100px;height:100px;position:absolute;top:50%;left:50%;transform:translate(-50%, -50%) scale(0);transform-origin:center;background-color:#02e502;border-radius:50%;z-index:-1;transition:.4s}.help__container .form button:hover{color:#000}.help__container .form button:hover::after{transform:translate(-50%, -50%) scale(3)}.help__container .confidence{font-size:14px;font-weight:400}